import React from 'react';
import { Button, Box } from '@mui/material';
import { CustomTabs } from '@/components';
import { usePerformInfoForm, usePerformContactsForm, usePerformRequisitesForm } from '@/core/hooks';
import { FormCustomerInfo, FormCustomerContacts, FormCustomerRequisites } from '@/containers';
import SaveIcon from '@mui/icons-material/Save';

function CreateLoanAgreement() {
    const [infoStates, infoHandlers, infoErrors] = usePerformInfoForm();
    const [contactState, contactHandlers, contactErrors] = usePerformContactsForm();
    const [requisiteState, requisiteHandlers, requisiteErrors] = usePerformRequisitesForm();

    return (
        <>
            <Box gridColumn="span 12" sx={{ display: 'flex', justifyContent: 'right' }}>
                <Button variant="contained" startIcon={<SaveIcon />} sx={{ background: '#0064ff', mr: 3 }}>
                    <span style={{ fontWeight: 'bold' }}>Сохранить</span>
                </Button>
            </Box>
            <Box gridColumn="span 12">
                <CustomTabs
                    tabs={[
                        {
                            tabName: 'Договор',
                            tabContent: <div>Договор</div>
                        },
                        {
                            tabName: 'Журнал действий',
                            tabContent: <div>Журнал действий</div>
                        },
                        {
                            tabName: 'Счета',
                            tabContent: <div>Счета</div>
                        },
                        {
                            tabName: 'Ставки',
                            tabContent: <div>Ставки</div>
                        },
                        {
                            tabName: 'Доп соглашения',
                            tabContent: <div>Доп соглашения</div>
                        },
                        {
                            tabName: 'Журнал событий',
                            tabContent: <div>Журнал событий</div>
                        },
                        {
                            tabName: 'Примечание',
                            tabContent: <div>Примечание</div>
                        }
                        // {
                        //     tabName: 'Реквизиты',
                        //     tabContent: (
                        //         <FormCustomerRequisites
                        //             requisiteState={requisiteState}
                        //             requisiteHandlers={requisiteHandlers}
                        //             requisiteErrors={requisiteErrors}
                        //         />
                        //     )
                        // }
                    ]}
                />
            </Box>
        </>
    );
}

export default CreateLoanAgreement;
